<template>
  <div class="view aside">
    <aside class="pickup">
      <c-pickup-tree :treeOptions="treeOptions" @node-click="handleNodeClick"></c-pickup-tree>
    </aside>
    <main class="main">
      <div class="tool-bar fixed">
        <el-button @click="add" :disabled="canAdd">新增</el-button>
        <el-button @click="showSomeThing">show</el-button>
      </div>
      <div class="tool-bar"></div>
      <el-collapse v-model="activeNames">
        <!-- title 用于显示在页面上，name用于展开的collapse与activeNames配合 -->
        <el-collapse-item :title="modelTableInfo.text" name="datagrid">
          <!-- 数据表格 -->
          <c-datagrid
            v-model="tableModel"
            :tableHeader="tableHeader"
            @clickDelete="handleTableDelete"
            @clickUpdate="handleTableUpdate"
          ></c-datagrid>
          <!-- 数据表格 -->
        </el-collapse-item>
      </el-collapse>

      <!-- dialog 编辑面板 -->
      <el-dialog
        title="新增"
        :visible.sync="dialogVisible"
        width="60%"
        :before-close="handleDialogClose"
      >
        <el-collapse v-model="activeNames">
          <!-- title 用于显示在页面上，name用于展开的collapse与activeNames配合 -->
          <el-collapse-item
            :title="panelDsl.title"
            :name="panelDsl.title"
            v-for="(panelDsl, panelDslIndex) in dslArray"
            :key="'panelDsl'+panelDslIndex"
          >
            <c-table
              class="box-card"
              v-model="modelDialog"
              :dsl="panelDsl.rows"
              :rules="rules"
              :tableInfo="panelDsl"
              ref="topTabe"
            />
          </el-collapse-item>
        </el-collapse>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="save">保存</el-button>
        </span>
      </el-dialog>
      <!-- dialog 编辑面板 -->
    </main>
  </div>
</template>

<script>
import {
  dslArray,
  asideTree,
  model,
  rules,
  tableHeader,
  tableModel
} from "./mock.js";
export default {
  name: "MainAdd",
  mounted() {
    this.activeNames = this.dslArray.map(panel => {
      return panel.title;
    });
    this.activeNames.push("datagrid");
  },
  data() {
    return {
      /* tableModel 展示列表需要的数据*/
      tableModel,
      tableHeader,
      /* 新增时需要的 类别名称和id*/
      modelTableInfo: {},
      /* 新增时需要的 类别名称和id*/
      /* dialog 弹出框的显隐*/
      dialogVisible: false,
      modelDialog: {},
      /* dialog */
      rules,
      dslArray,
      asideTree,
      activeNames: ["项目参数"],
      /* 动态数据 */
      model /* 用于保存aside被点击时的数据 */
    };
  },
  methods: {
    showSomeThing(...args) {
      console.log("showSomeThing", this.tableModel);
      console.log("showSomeThing", args);
    },
    /* 数据表格 */
    handleTableDelete(row, scope) {
      this.$confirm(`确实要删除当前的表单数据?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          const loading = this.$loading();
          let target;
          const index = this.tableModel.findIndex(d => d.id === row.id);
          target = this.tableModel.splice(index, 1);
          /* 发送target到后台处理删除请求 */

          console.log("remove", target, this.asideTree);
          setTimeout(() => {
            this.afterRemove(loading);
          }, 1000 * 2);
        })
        .catch(error => {
          console.log("cancel", error);
        });

      console.log("removeRowFromtableModel", row, scope);
    },
    handleTableUpdate(row, scope) {
      /* 打开dialog */
      /* current */
      let temp = {
        lbmc: this.modelTableInfo.text,
        lbbm: this.modelTableInfo.id,
        mc: "",
        bm: "",
        ...row
      };
      console.log("handleTableUpdate", temp, row, scope);
      this.modelDialog = temp;
      this.dialogVisible = true;
    },
    afterRemove(loading) {
      /* 置空 */
      this.modelDialog = {};
      loading.close();
      this.$message({
        showClose: true,
        message: "删除成功",
        type: "success"
      });
      /* 刷新列表 */
      // this.query();
    },
    /* 数据表格 */
    /* dialog */
    add() {
      /* current */
      let temp = {
        lbmc: this.modelTableInfo.text,
        lbbm: this.modelTableInfo.id,
        mc: "",
        bm: ""
      };
      this.modelDialog = temp;
      this.dialogVisible = true;
    },
    save() {
      /* current */
      function isOk(tables) {
        let count = tables.length;
        let isAllOk = true;
        return new Promise(resolve => {
          tables.map(table => {
            table.valid().then(isSuccess => {
              --count;
              isAllOk = isAllOk && isSuccess;
              if (!isAllOk || count === 0) resolve(isAllOk);
            });
          });
        });
      }
      /* 验证表单是否都符合rule */
      isOk(this.$refs.topTabe).then(isAllOk => {
        /* 需要保存的是在dialog里面的modelDialog */
        let doc = this.modelDialog;
        if (isAllOk) {
          const loading = this.$loading();
          console.log("this.currentModelAdd");
          /* save */
          this.$post("/proxy/save", {
            coll: "hqbj_dm_sjzd" /* collection */,
            doc /* document */
          }).then(response => {
            if (response.isSuccess) {
              /* 根据doc是否已有id判断类型为新增或者更新 */
              if (doc.zdxmid) {
                /* update */
                const index = this.tableModel.findIndex(d => {
                  debugger;
                  return d.zdxmid === doc.zdxmid;
                });
                /* 如果是修改，需要更新currentModel */
                this.tableModel.splice(index, 1, doc);
              } else {
                /* add 这里的doc应该是从response里取出来带有id，已经存入数据库的doc*/
                this.tableModel.splice(this.tableModel.length, 0, doc);
              }

              loading.close();
              this.$router.push({ name: "home" });
              this.$message({
                showClose: true,
                message: "保存成功",
                type: "success"
              });
            } else {
              this.$message({
                showClose: true,
                message: "保存失败",
                type: "warning"
              });
            }
          });
        } else {
          this.$message({
            showClose: true,
            message: "请按照提示检查输入项",
            type: "warning"
          });
        }
      });
      this.dialogVisible = false;
    },
    handleDialogClose(done) {
      this.$confirm("确认关闭？")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    /* dialog */
    handleNodeClick(data, node, vm) {
      /* 目前是asideTree前11个项带有data，但是如果没有，就需要根据传过来的id从后台查询再赋值 */
      if (node.isLeaf && data && data.data) {
        this.tableModel = data.data;
        this.modelTableInfo = data;
      }
      console.log(node, vm);
    }
  },
  computed: {
    /* 根据modelTableInfo及id判断是否语序添加项目*/
    canAdd() {
      return !(this.modelTableInfo && this.modelTableInfo.id);
    },
    treeOptions() {
      let treeOptions = {
        props: {
          children: "children",
          label: "text"
        },
        showCheckbok: false
      };
      treeOptions.data = this.asideTree;
      return treeOptions;
    }
  },
  watch: {
    tableModel(newV) {
      console.log(newV);
    }
  },
  components: {
    CTable: () =>
      import(/* webpackChunkName: "FormTable" */ "@c/FormTable.vue"),
    CPickupTree: () =>
      import(/* webpackChunkName: "PickupTree" */ "@c/PickupTree.vue"),
    CDatagrid: () =>
      import(/* webpackChunkName: "PickupTree" */ "@c/Datagrid.vue")
  }
};
</script>

