function _map(array, fn) {
    let target = [];
    for (let index = 0; index < array.length; index++) {
        const element = array[index];
        target.push(fn(element, index));
    }
    return target;
}

let arr = new Array(0);
let stamp = _map(arr, (ele, index) => ({
    dm: "",
    mc: "",
    sd: "",
    wh: "",
    id: "",
}));

