import Vue from 'vue'
import Vuex from 'vuex'

import {
  model,
} from "./mock.js";


Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    currentModelDialog: model,
  },
  actions: {
    setCurrentPath({
      commit
    }, path) {
      return commit('setCurrentPath', path)
    }
  },
  mutations: {
    setcurrentModelDialog(state, currentModelDialog) {
      console.log("currentModelDialog", currentModelDialog)
      state.currentModelDialog = currentModelDialog;
    },
    setcurrentModelDialogAdd(state, currentModelDialogAdd) {
      console.log("currentModelDialogAdd", currentModelDialogAdd)
      state.currentModelDialogAdd = currentModelDialogAdd;
    },
  }
})